export default function todoLists(state = {}, action) {
    switch (action.type) {
        case 'ADD_TODO_LIST':
            return state.concat(action.list);
        case 'DELETE_TODO_LIST':
            return state.filter((list) => { return list.id !== action.id });
        case 'DELETE_TASK':
            let listIndex;
            let taskIndex;
            let todoListsCopy = [];

            state.forEach(item => {
                todoListsCopy.push(item)
            });

            console.log(todoListsCopy);
            console.log(state);

            todoListsCopy.find((list, index) => {
                if (list.id === action.listId) {
                    listIndex = index;
                    return true;
                }
                return false;
            });

            todoListsCopy[listIndex].tasks.find((task, index) => {
                if (task.id === action.taskId) {
                    taskIndex = index;
                    return true;
                }
                return false;
            });

            todoListsCopy[listIndex].tasks.splice(taskIndex, 1);

            console.log(todoListsCopy);
            console.log(state);

            return todoListsCopy;
        default:
            return state
    }
}
